import { React, useState } from 'react';
import ReactAudioPlayer from 'react-audio-player';

import Katusha from '../music/katusha.mp3';
import KatImage from '../img/kat.jpg';
import TNImage from '../img/tnoch.jpg';
import TNoch from '../music/tnoch.mp3';
import SmuglImage from '../img/smugl.jpg';
import Smugl from '../music/smugl.mp3';
import PlatokImage from '../img/platok.jpg';
import Platok from '../music/platok.mp3';
import DorogiImage from '../img/dorogi.jpg';
import Dorogi from '../music/dorogi.mp3';
import SvashImage from '../img/svash.jpg';
import Svash from '../music/svash.mp3';

function isPlaying(target) {
    let targetPos = {
        bottom: document.querySelector('#' + target).getBoundingClientRect().bottom + window.pageYOffset,
        top: window.pageYOffset + document.querySelector('#' + target).getBoundingClientRect().top
    }
    let windowPos = {
        top: window.pageYOffset,
        bottom: window.pageYOffset + document.documentElement.clientHeight
    }
    if (targetPos.bottom > windowPos.top && targetPos.top < windowPos.bottom) {
        return true;
    } else { return false; }  
}

export default function Article(props) {
    const [katPlaying, setKatPlaying] = useState(false);
    const [tnochPlaying, setTnochPlaying] = useState(false);
    const [smuglPlaying, setSmuglPlaying] = useState(false);
    const [platokPlaying, setPlatokPlaying] = useState(false);
    const [dorogiPlaying, setDorogiPlaying] = useState(false);
    const [svashPlaying, setSvashPlaying] = useState(false);

    const [volume, setVolume] = useState(true);

    window.addEventListener('scroll', function() {
        setKatPlaying(isPlaying('kat'));
        setTnochPlaying(isPlaying('tnoch'));
        setSmuglPlaying(isPlaying('smugl'));
        setPlatokPlaying(isPlaying('platok'));
        setDorogiPlaying(isPlaying('dorogi'));
        setSvashPlaying(isPlaying('svash'));
    });

    const volumeOff = (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16" onClick={() => setVolume(true)} fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" clipRule="evenodd" />
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2" />
        </svg>
    );

    const volumeOn = (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16" onClick={() => setVolume(false)} fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.536 8.464a5 5 0 010 7.072m2.828-9.9a9 9 0 010 12.728M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" />
        </svg>
    );

    return (
        <div className='bg-white mx-auto items-center w-full'>
            <h2 className='fixed bottom-5 right-5'>{volume ? volumeOn : volumeOff}</h2>
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Приглашаем в путешествие 🎧</h1>
            <div class='container mx-auto'>
                <p class='text-3xl mb-8'>
                Это будет необычное путешествие. По мере чтения нашего материала вы будете слушать музыку, чтобы полностью погрузиться в атмосферу происходящего. Всё будет происходить само, наслаждайтесь :)<br />
                <br />
                Однако если рядом с вами кто-то спит, или что-то ещё не позволяет вам воспроизводить звук, вы можете выключить его, нажав на иконку в правом нижнем углу. Сейчас звук <span class='font-bold'>{volume ? 'включен' : 'выключен'}</span>. 
                </p>
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Почему именно музыка? 🤔</h1>
            <div class='container mx-auto'>
                <p class='text-3xl mb-8'>
                Великая Отечественная война - одна из самых героических страниц нашей истории. Мужество людей, их стремление защитить свою Родину не знало границ. Вместе с людьми в солдатский строй встала песня. С первых дней войны песни помогали народу выстоять и победить, они стали оружием, которое подавляет врага.<br />
                <br />
Война безжалостно отбирала у людей самое дорогое: любовь, родных, уверенность в завтрашнем дне, детство и счастье, даже надежду, но всегда у людей оставалось одно — музыка.<br />
<br />
Прошли годы, но песни военных лет звучат и сегодня, трогая сердца наших современников. У каждой песни есть своя судьба, своя история. Каждая из них раскрывает нам отдельную страницу тех незабываемых событий.<br />
<br />
Роль песни в годы войны была чрезвычайно высокой. Она складывалась обо всем, что происходило на фронте и в тылу, что согревало души и воспевало великий подвиг русского народа. В них говорилось о патриотизме, о солдатской дружбе, о любви. Музыка поддерживала людей в трудные минуты, утешала их измучившиеся души. Она была необходима человеку как воздух, с ней человеческое сердце не черствело и находило в себе новые силы хранить надежду. У каждой песни этого времени своя история, свой путь, и своя судьба.<br />
                </p>
                <hr class='bg-red-600 h-1' />
            </div>
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Катюша - очень важная военная песня ☄️</h1>
            <div class='container mx-auto' id='kat'>
                <p class='text-3xl mb-8'>
                К 1938 году ровесник века, талантливый поэт, Михаил Исаковский, начавший свою творческую карьеру ещё в юном возрасте на родной Смоленщине, уже семь лет успешно работал в Москве. Песни на его стихи, успешно распевались по всей стране. К созданию своих текстов Михаил Васильевич подходил очень ответственно и поэтому работал над ними неспешно, тем не менее поэт на одном дыхании написал два четверостишия о девушке с именем Катюша, которая выйдя на берег реки, запела песню о своём возлюбленном.<br />
                <br />
                Через несколько месяцев, после написания четверостиший, а точнее в апреле, произошла встреча, явившаяся для будущей знаменитой песни не иначе как судьбоносной. В литературном отделе газеты «Правда» поэт Михаил Исаковский познакомился с композитором Матвеем Блантером.<br />
                <br />
                В разговоре композитор поинтересовался у популярного поэта – нет ли у него стихов, к которым можно написать музыку и соответственно преобразить их в песню. Вот тогда-то Михаил Васильевич и вспомнил про свои незаконченные стихи о девушке с именем Катюша.<br />
                <br />
                Премьерное исполнение песни «Катюша», как и обещал Блантер, состоялась на дебютном концерте Государственного джаз-оркестра под управлением Виктора Кнушевицкого в ноябре 1938 года в Колонном зале Дома Союзов. Композиция была так вдохновенно исполнена двадцатидвухлетней Валентиной Батищевой, что зал захлебнулся в овациях слушателей, просивших певицу ещё три раза «на бис» исполнить эту песню.<br />
                <br />
                Во время Великой Отечественной войны ласковым именем «Катюша» окрестили боевую машину залпового огня БМ-13.
                </p>
                <img class='w-full mt-8 mb-8' src={KatImage} alt='Катюша' />
                <ReactAudioPlayer
                    src={Katusha}
                    autoPlay
                    loop
                    muted={!katPlaying || !volume}
                />
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Народное творчество 🎸</h1>
            <div class='container mx-auto' id='tnoch'>
                <p class='text-3xl mb-8'>
                Песни фронтовых дорог – они особенные. «Тёмная ночь» считается одной из самых любимых и популярных песен, написанных за годы Великой Отечественной войны. <br />
<br />
В 1943 году во время работ над фильмом «Два бойца» у режиссера картины Леонида Лукова возникли трудности со съемкой эпизода, где солдат пишет письмо домой. После многих неудачных попыток режиссеру пришла в голову мысль, что исполненная героем фильма песня лучше всего передаст чувства бойца, который пишет письмо близким.<br />
<br />
Всенародной «Темная ночь» стала в 1943 году, после того, как зрители услышали её из уст Марка Бернеса, который в роли Аркадия Дзюбина – главного героя картины — поет ее ночью под гитару, в землянке с протекающей крышей, в окружении фронтовых товарищей.
                </p>
                <img class='w-full mt-8 mb-8' src={TNImage} alt='Марк Бернес исполняет темную ночь в фильме два бойца' />
                <ReactAudioPlayer
                    src={TNoch}
                    autoPlay
                    loop
                    muted={!tnochPlaying || !volume}
                />
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>От фольклора до всемирно известных мелодий 🎻</h1>
            <div class='container mx-auto' id='smugl'>
                <p class='text-3xl mb-8'>
                «Смуглянка» была написана за тридцать с лишним лет до выхода знаменитой киноленты о команде бравых лётчиков-истребителей, в далёком 1940 году. Автором текста «Смуглянки» стал знаменитый советский прозаик и поэт Яков Захарович Шведов. Музыка же «Смуглянки» принадлежит перу советского композитора, педагога и хорового дирижёра Анатолия Григорьевича Новикова. <br />
<br />
При написании «Смуглянки» её авторы вдохновлялись молдавским фольклором, поэтому произведение получилось лирическим, задорным и игровым. Впрочем, в довоенное время «Смуглянку» так никому услышать и не довелось, а когда началась война, ноты её были утеряны, и на руках у Новикова остались лишь черновики. Восстановив песню на их основе, композитор отправился на радио, где «Смуглянку» тогдашние редакторы раскритиковали в пух и прах, укорив заодно Новикова в том, что на фоне столь тяжелых событий он предлагает песни о любви, свиданиях и расставаниях.<br />
<br />
«Вторую жизнь» и всенародную любовь песне подарил Леонид Быков, снявший в 1973 году кинопроизведение, что вошло в золотой фонд советской киноиндустрии – фильм «В бой идут одни старики». Быков, будучи режиссёром картины, не только смог великолепно отыграть в ней главную роль, но и исполнить «Смуглянку» так, какой её запомнило большинство жителей СССР.
                </p>
                <img class='w-full mt-8 mb-8' src={SmuglImage} alt='Смуглянка' />
                <ReactAudioPlayer
                    src={Smugl}
                    autoPlay
                    loop
                    muted={!smuglPlaying || !volume}
                />
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Священная война - "народный гимн" войны 🎤</h1>
            <div class='container mx-auto' id='svash'>
                <p class='text-3xl mb-8'>
                Именно с Белорусского вокзала уходили летом 1941 года советские войска и именно здесь, 26 июня 1941-го, впервые прозвучала песня Александра Васильевича Александрова «Священная война», которая стала своеобразным гимном на время войны: её исполняли и на фронте, и в партизанских отрядах, и на заводах в тылу. <br />
<br />
Эта песня была совершенно необходима в те грозные дни. С этой песней на вокзалах родные и близкие провожали солдат на смертельный бой с фашизмом. Проникновенные слова и величавая мелодия песня звучала как воинская присяга.<br />
<br />
Песня «Священная война» приобрела массовую популярность на фронтах Великой Отечественной войны и поддерживала высокий боевой дух в войсках, особенно в тяжёлых оборонительных боях. С октября 1942 года возобновилось производство грампластинок на Апрелевском заводе, прерванное войной. На фронт вместе с боеприпасами, пушками и танками отправляли грампластинки с военными песнями.
                </p>
                <img class='w-full mt-8 mb-8' src={SvashImage} alt='Священная война' />
                <ReactAudioPlayer
                    src={Svash}
                    autoPlay
                    loop
                    muted={!svashPlaying || !volume}
                />
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Синий платочек 🎷</h1>
            <div class='container mx-auto' id='platok'>
                <p class='text-3xl mb-8'>
                Счастливая и необычная судьба у этой песни: она родилась дважды. Впервые прозвучала ее незатейливая мелодия в довоенном 1939 году, когда, спасаясь от фашистской неволи, в Советский Союз приехали участники популярного польского эстрадного коллектива «Голубой джаз».. Весной 1940 года они гастролировали в Москве. Концерты проходили в саду «Эрмитаж». Именно в московском саду «Эрмитаж» оценил польских музыкантов поэт Яков Маркович Галицкий. Услышав, как композитор и пианист оркестра Ежи Петербурский исполняет на пианино красивую лирическую зарисовку (первые её наброски, к слову, Петербурский написал ещё в Минске в 1940 году), Галицкий постарался удержать в голове мелодию и вскоре быстро набросал на неё текст о «синем платочке, падающем с опущенных плеч». Как только концерт закончился, Галицкий связался с музыкантами и предложил им уже готовый текст, который они с радостью приняли.<br />
<br />
Сочетание незатейливого текста и простой мелодии приглянулось слушателям, и песня быстро распространилась по эстраде. В свой репертуар её взяли многие известные артисты СССР: Михаил Гаркави, Лидия Русланова, Вадим Козин.<br />
<br />
Ещё одной знаменитой исполнительницей «Платочка» стала молодая певица Клавдия Шульженко, которая со своим мужем Владимиром Коралли давала концерты на фронте. Зимой 1942-го судьба свела её с лейтенантом Михаилом Максимовым, который, по одной из версий, предложил ей авторский вариант текста на полюбившуюся солдатам композицию. Изменения коснулись, по большей части, только концовки песни: в ней нашла отражение патриотическая тематика и борьба советского народа с фашизмом.
                </p>
                <img class='w-full mt-8 mb-8' src={PlatokImage} alt='Клавдия Шульженко' />
                <ReactAudioPlayer
                    src={Platok}
                    autoPlay
                    loop
                    muted={!platokPlaying || !volume}
                />
                <hr class='bg-red-600 h-1' />
            </div>  
            <h1 class='text-5xl font-bold text-center mt-8 mb-8'>Эх, дороги! 🥁</h1>
            <div class='container mx-auto' id='dorogi'>
                <p class='text-3xl mb-8'>
                «Дороги» сочинили поэт-песенник Лев Ошанин и композитор Анатолий Новиков. Они написали её по заказу Ансамбля НКВД. Очень известный и популярный в те времена московский коллектив специализировался на театрализованных выступлениях, в которых все номера были объединены единым сюжетом. Главным режиссёром ансамбля был Сергей Юткевич.<br />
<br />
Как вспоминает Ошанин, «все строчки песни оказались совсем короткими. Было очень трудно положить на них естественные и емкие слова и заставить эти слова рассказывать».<br />
<br />
Ансамбль исполнил песню. Встретили ее восторженно, но авторы все равно были недовольны. Авторы почувствовали, чего не хватает песне, чтобы она ещё пронзительнее отразила свою тему. Песню основательно переработали, и стала она, кстати, называться не «Солдатские дороги», как сначала, не «Эх, дороги», как потом, а просто «Дороги».
                </p>
                <img class='w-full mt-8 mb-8' src={DorogiImage} alt='Дороги' />
                <ReactAudioPlayer
                    src={Dorogi}
                    autoPlay
                    loop
                    muted={!dorogiPlaying || !volume}
                />
            </div>  
        </div>
    );
}